.funCard {
  display: flex;
  flex-direction: column;
  margin-bottom: 5px;
  border-radius: 10px;
  transition-duration: 500ms;
}
.ket {
  transition-duration: 100ms;
}

.funCard:hover {
  height: 100%;
  width: 100%;
  background-color: #a5f3fceb;
}

.funCard:hover > .img_box {
  transform: scale(0.8);
  transition-duration: 1s;
}

.img_box {
  justify-self: center;
  align-self: center;
  margin-top: 20px;
  border-radius: 10px;
  width: 50%;
  height: 50%;
  background-color: aliceblue;
  transition-duration: 1s;
  background-size: cover;
  background-position: center;
}

.ket {
  border-radius: 10px;
  position: relative;
  top: 20px;
  width: 100%;
  background-color: #78f1feeb;
}
